import React from 'react';
import './App.css';
import Team from './Team';
import Home from './Home';
import Head from './Head';
import Bottom from './Bottom'
import Research from './Research';

import {
    BrowserRouter as Router,
    Switch,
    Route,
    HashRouter,
} from "react-router-dom";

// basename={process.env.PUBLIC_URL}

function App() {
    
    return (
	<div class="container justify-content-md-center">
	    <HashRouter >
		<Switch>
		    <Route exact path="/">
			<Home />
		    </Route>
		    <Route path="/research">
			<Research />
		    </Route>
		    <Route path="/research/publications">
			<Research />
		    </Route>
		    <Route path="/research/models">
			<Developping />
		    </Route>
		    <Route path="/research/databases">
			<Developping />
		    </Route>
		    <Route path="/industry">
			<Developping />
		    </Route>
		    <Route path="/team">
			<Team />
		    </Route>
		    <Route path="/corperation">
			<Developping />
		    </Route>
		    <Route path="/about">
			<About />
		    </Route>
		</Switch>
	    </HashRouter>
	</div>
    );

}



function About() {
    return (
	<div>
	    <Head />
	    <h2>About</h2>
	    <Bottom />
	</div>
    );
}

function Developping() {
    return (
	<div>
	    <Head />
	    <h1>Developing...</h1>	    
	    <Bottom />
	</div>
    );
}

export default App;
